    align-items: center;
  }

  .Projects .Images .ImageContainer {
    display: flex;  
    /* background: black; */
    margin: 2%;
    max-width: 100%;
    /* height: 65vh; */
    flex-direction: column;
    align-items: center;
    /* border-radius: 5px;
    box-shadow: 0px 0px 5px 1px white; */
  }

  .Projects .ImageContainer .Thumbnail img {
    max-width: 90vw;
    /* border-radius: 5%; */
    /* border: 1px solid white; */
    /* box-shadow: 0px 0px 5px 1px white; */
    padding: 1%;
    cursor: pointer;
  }

  .Projects .ImageContainer .Text {
    padding: 0 5% 5% 5%;
    width: 90vw;
  }

